.wrapper {
	max-width: 100%;
	height: 100vh;
	background-color: #f3f3f3;
	display: flex;
	justify-content: center;
	color: #6e7c8c;
}
.sudoku {
	padding: 0 10px;
		&__header{
			display: flex;
			margin: 100px 0 40px 0;
			font-size: 30px;
			justify-content: center;
		}

		&__level {
			font-weight: 600;
			margin-right: 20px;
			
		}

		&__level-button{
			padding: 10px 20px;
			border-radius: 5px;
			font-size: 20px;
			background-color: #f3f3f3;
			
			&:hover{
				background-color: rgba($color: #6e7c8c, $alpha: .5);
			}
		}

		&__error {
			margin: 0 15px;
			display: flex;
			align-items: center;
		}

		&__timer {
			display: flex;
			img{
				filter: brightness(0) invert(0.5);
				cursor: pointer;
			}
		}

		&__time{
			margin-right: 10px;
			display: flex;
			align-items: center;
		}

		&__timer-play{

		}

		&__timer-pause{
			display: flex;
			align-items: center;
		}

		&__game {
			width: 100%;
			display: flex;
			justify-content: center;
		}

		&__field {
			position: relative;
			table{
				border-collapse: collapse;
				border: 2px solid black;
			}

			td{
				width: 40px;
				height: 40px;
				border: 1px solid black;
				text-align: center;
				font-size: 20px;
				position: relative;
			}
		}

		&__additional-elements{
			width: 25%;
			display: flex;
			flex-direction: column;
		}

		&__level-container{
			display: inline;
			@media (max-width:550px) {
				display: block;
				position: relative;
				cursor: pointer;
				&:hover{
					.sudoku__level-li{
						@media (max-width:550px) {
							display: block;
						}
					}
				}
			}
			ul{
				display: none;
				@media (max-width:550px) {
					padding-top: 27px;
					display: block;
					position: absolute;
					z-index: 1000;
					top: 0;
					left: 0;
				}
			}
		}

		&__level-li{
			display: none;
			@media (max-width:550px) {
				display: none;
			}
		}
		
		&__level-button_550px{
			display: none;
			@media (max-width:550px) {
				display: block;
				padding: 10px;
				width: 100%;
				font-size: 15px;
				color: black;
				background-color: #f3f3f3;
				
				&:hover{
					background-color: #6e7c8c;
				}
			}
		}

		&__numpad {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			position: relative;
		}

		&__numpad-item {
			padding: 20px 30px;
			background-color: #eaeef4;
			margin: 10px;
			border-radius: 5px;
			transition: all .3s ease 0s;
			span{
				font-size: 40px;
				color: blue;
			}
			&:hover{
				background-color: #c9cdd2;
				cursor: pointer;
			}
		}

		&__button {
			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 5px;
			background-color: #0072e3;
			color: white;
			width: 80%;
			padding: 10px;
			margin: 10px;
			font-size: 30px;
			transition: all .3s ease 0s;
			&:hover{
				background-color: #0257ad;
			}
		}
}

//preloader

.preloader {
	width: 361.6px;
	height: 361.6px;
	background-color: #f3f3f3;

	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;

	&__img {
	}

	&__not-active {
		display: none;
	}
}

.hidden-element{
	position: absolute;
	z-index: 100;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: white;
	span{
		position: absolute;
		top: 25%;
		left: 35%;
	}
}

.clicked{
	background-color: #7fbcf8;
}

.illumination{
	background-color: #c2dbf5;
}

.clicked.error{
	color: red;
	background-color: #7fbcf8;
}
.error{
	color: black;
	background-color: rgb(143, 25, 25);
}

.active{
	color: #0257ad;
}

._pause{
	position: absolute;
	top: 0;
	left: 0;
	background-color: white;
	z-index: 150;
	width: 100%;
	height: 101%;
	cursor: pointer;
	&::after{
		content: '';
		position: absolute;
		top: 37%;
		left: 42%;
		background-image: url(~/components/sudoku/img/pause-play.png);
		width: 64px;
		height: 64px;
	}
}

