@media (max-width:1250px) {
	.sudoku__header{
		margin-top: 20px;
		margin-bottom: 20px;
	}
	.sudoku__game{
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	.sudoku__additional-elements{
		width: 50%;
	}
	.sudoku__numpad-item{
		margin: 2px;
		padding: 15px 23px;
	}
	.sudoku__field{
		td{
			width: 35px;
			height: 35px;
		}
	}
}

@media (max-width:700px) {
	.sudoku__level-button{
		padding:10px 5px 10px 5px ;
	}
	.sudoku__error{
		font-size: 25px;
	}
	.sudoku__time{
		font-size: 25px;
	}
}

@media (max-width:550px) {
	.sudoku__level-button{
		display: none;
	}
	.sudoku__numpad-item{
		margin: 1px;
		padding: 7px 10px;
	}
	.sudoku__field{
		td{
			width: 30px;
			height: 30px;
		}
	}

	.sudoku__level{
		margin-right: 0;
	}
} 
